import React, { useState } from 'react'
import "../Logo.css"
import AddCircleOutlineSharpIcon from '@mui/icons-material/AddCircleOutlineSharp';

import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
export default function Box() {

    const [item, setitem] = useState([]);

    const [text, setText] = useState({
        title: "",
        area: ""
    })


    const addItem = () => {


          setitem((prev)=>{
              
              return [...prev,text]
            });
    }

    const inputEvent = (event) => {

        const { name, value } = event.target

        setText((preval) => {
            return {
                ...preval,
                [name]: value
            }
        })
    }



    return (
        <>
            <div className='box'>
                <div className="main">

                    <form action="">
                        <input type="text" onChange={inputEvent} placeholder='Title' name='title' value={text.title} />
                        <textarea name="area" onChange={inputEvent} id="" cols="" rows="" placeholder='Take a note...' value={text.area}></textarea>
                        <AddCircleOutlineSharpIcon id="addbutton" onClick={addItem} />
                    </form>
                </div>
            </div>

<div className='item-list'>


                   

               {
                   
                   item.map((currval,index)=>{
                       return (
                           <>
                       <div  key={index} id={index} className="output">
                       <br />
                       <br />
                       <h6> {currval.title} </h6>
                       <p  >{currval.area} </p>
                       <DeleteOutlineIcon className="outbtn" />
                       
                </div>
                       </> )
                    })
                }
                </div>  

        </>
    )
}
